import { BASE_URL } from './constants';
import { handleValidityUrl } from './helpers';

// —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è –Ω–æ–≤–æ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
export const createUser = (email, password, name) => fetch(`${BASE_URL}/signup`, {
  method: 'POST',
  headers: { 'Content-Type': 'application/json' },
  body: JSON.stringify({ name, email, password }),
})
  .then((res) => {
    if (res.status !== 409) {
      return res.json();
    }
    return Promise.reject(res.status);
  })
  .catch((err) => Promise.reject(err));

// –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—è —Å—É—â–µ—Å—Ç–≤—É—é—â–µ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
export const authorizeUser = (email, password) => fetch(`${BASE_URL}/signin`, {
  method: 'POST',
  headers: { 'Content-Type': 'application/json' },
  body: JSON.stringify({ email, password }),
})
  // eslint-disable-next-line consistent-return
  .then((res) => {
    if (res.ok) {
      return res.json();
    }
    return Promise.reject(res.status);
  })
  // eslint-disable-next-line consistent-return
  .then((data) => {
    if (data.token) {
      localStorage.setItem('jwt', data.token);
      return data;
    }
  })
  // eslint-disable-next-line prefer-promise-reject-errors
  .catch((err) => Promise.reject(err));

// –ø—Ä–æ–≤–µ—Ä–∫–∞ —Ç–æ–∫–µ–Ω–∞
export const getUserContent = (token) => fetch(`${BASE_URL}/users/me`, {
  method: 'GET',
  headers: {
    'Content-Type': 'application/json',
    Authorization: `Bearer ${token}`,
  },
})
  .then((res) => {
    if (res.ok) {
      return res.json();
    }
    return '';
  })
  .then((data) => data)
  .catch((err) => Promise.reject(new Error(`Error: ${err}`)));

/// –ø–æ–ª—É—á–µ–Ω–∏–µ —Å–µ—Ä–≤–µ—Ä–Ω—ã—Ö –∫–∞—Ä—Ç–æ—á–µ–∫
export const getAllArticles = () => fetch(
  `${BASE_URL}/articles`,
  {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
      Authorization: `Bearer ${localStorage.getItem('jwt')}`,
    },
  },
)
  .then((res) => res.json())
  .catch((err) => Promise.reject(new Error(`Error: ${err}`)));

// —É–¥–∞–ª–µ–Ω–∏–µ —Å–µ—Ä–≤–µ—Ä–Ω–æ–π –∫–∞—Ä—Ç–æ—á–∫–∏
export const deleteArticle = (article) => fetch(
  `${BASE_URL}/articles/${article._id}`,
  {
    method: 'DELETE',
    headers: {
      'Content-Type': 'application/json',
      Authorization: `Bearer ${localStorage.getItem('jwt')}`,
    },
  },
)
  .then((res) => res.json())
  .catch((err) => Promise.reject(new Error(`Error: ${err}`)));

// —Å–æ—Ö—Ä–∞–Ω–∏—Ç—å –∫–∞—Ä—Ç—É –≤ —Å–≤–æ–µ–º –∞–ø–∏
export const saveArticle = (article, keyword) => {
  const {
    title,
    description,
    publishedAt,
    source,
    url,
    urlToImage,
  } = article;

  return fetch(
    `${BASE_URL}/articles`,
    {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${localStorage.getItem('jwt')}`,
      },
      body: JSON.stringify({
        keyword,
        title,
        text: description || title,
        date: publishedAt,
        source: source.name,
        link: url,
        image: handleValidityUrl(urlToImage) ? urlToImage : 'https://i.ibb.co/3SkSn1X/news-default.jpg', // –≤—Å–µ —Ä–∞–≤–Ω–æ –±—É–¥–µ—Ç –±–∞–≥ –µ—Å–ª–∏ —Å –∞–ø–∏ –ø—Ä–∏–ª–µ—Ç–∞–µ—Ç –∫—Ä–∏–≤–∞—è —Å—Å—ã–ª–∫–∞ –ø–æ —Ç–∏–ø—É http://rg.ru/cgi-?http://.
      }),
    },
  )
    .then((res) => res.json())
    .catch((err) => Promise.reject(new Error(`Error: ${err}`)));
};
