import React, { useEffect } from 'react';
import './Register.css';
import PopupWithForm from '../PopupWithForm/PopupWithForm';
import Validator from '../../utils/Validator';

function Register({
  isOpen, onClose, onSwitchPopup, onRegister, textError, isFieldDisabled,
}) {
  const {
    values, handleChange, errors, isValid, resetForm,
  } = Validator();

  useEffect(() => {
    resetForm();
  }, [isOpen]); // –¥–æ–∫—Ä—É—Ç–∏—Ç—å –≤–∞–ª–∏–¥–∞—Ü–∏—é

  function handleSubmit(e) {
    e.preventDefault();

    onRegister(values.email, values.password, values.name);
  }

  return (
    <PopupWithForm
      isOpen={isOpen}
      onClose={onClose}
      onSwitchPopup={onSwitchPopup}
      onSubmit={handleSubmit}
      name='register'
      title='–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è'
      text='–∏–ª–∏ '
      buttonDescription='–í–æ–π—Ç–∏'
    >
      <label className="popup__label">Email</label>
      <input
        value={values.email || ''}
        onChange={handleChange}
        id="email-register"
        className="popup__input popup__input_type_email"
        type="email"
        name="email"
        placeholder="–í–≤–µ–¥–∏—Ç–µ –ø–æ—á—Ç—É"
        minLength="5"
        maxLength="30"
        required
      />
      <span id="email-register-error" className="popup__error" >{errors.email || ''}</span>

      <label className="popup__label">–ü–∞—Ä–æ–ª—å</label>
      <input
        value={values.password || ''}
        onChange={handleChange}
        className="popup__input popup__input_type_password"
        id="password-register"
        type="password"
        name="password"
        placeholder="–í–≤–µ–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å"
        minLength="5"
        maxLength="30"
        disabled={isFieldDisabled}
        required
      />
      <span id="password-error-register" className="popup__error" >{errors.password || ''}</span>

      <label className="popup__label">–ò–º—è</label>
      <input
        value={values.name || ''}
        onChange={handleChange}
        className="popup__input popup__input_type_name"
        id="name"
        type="text"
        name="name"
        placeholder="–í–≤–µ–¥–∏—Ç–µ —Å–≤–æ—ë –∏–º—è"
        minLength="2"
        maxLength="30"
        disabled={isFieldDisabled}
        required
      />
      <span id="name-error-register" className="popup__error"> {errors.name || ''} </span>
      <span id="main-error-register" className="popup__error popup__error_type_register">{textError}</span>
      <button className={`popup__submit ${isValid ? 'popup__submit_type_active' : ''}`} type="submit" disabled={!isValid}>–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</button>
    </PopupWithForm>
  );
}

export default Register;
