import React, { useContext } from 'react';
import './summary.css';
import CurrentUserContext from '../../context/CurrentUserContext';

function Summary({ mySavedArticles }) {
  const currentUser = useContext(CurrentUserContext);

  // —Å–æ–±–∏—Ä–∞–µ–º –º–∞—Å—Å–∏–≤ —É–Ω–∏–∫–∞–ª—å–Ω—ã—Ö –∫–ª—é—á–µ–≤—ã—Ö —Å–ª–æ–≤
  const myArray = mySavedArticles.map((item) => item.keyword);
  const mySavedKeywords = [...new Set(myArray)];

  function handleKeywordFormatter(str) {
    if (!str) {
      return str;
    }
    return str[0].toUpperCase() + str.slice(1);
  }

  const firstKeyword = `${handleKeywordFormatter(mySavedKeywords[0])}`;
  const secondKeyword = `${handleKeywordFormatter(mySavedKeywords[1]) || ''}`;
  const thirdKeyword = `${handleKeywordFormatter(mySavedKeywords[2]) || ''}`;
  const keywords = `${mySavedKeywords.length >= 3 ? ` ${firstKeyword}, ${secondKeyword}, ${thirdKeyword}` : ` ${firstKeyword} –∏ ${secondKeyword}`}`;
  const count = mySavedArticles.length;
  function handleTitleSaveNewsRewritter(foo) {
    if (foo === 0 && foo >= 5) { return '—ã—Ö —Å—Ç–∞—Ç–µ–π'; }
    if (foo === 1) { return '–∞—è —Å—Ç–∞—Ç—å—è'; }
    if (foo > 1 && foo < 5) { return '—ã—Ö —Å—Ç–∞—Ç—å–∏'; }
    return '—ã—Ö —Å—Ç–∞—Ç–µ–π';
  }

  function handleSubtitleRewritter(foo) {
    if (foo === 1) { return '–æ–º—É —Å–ª–æ–≤—É'; }
    return '—ã–º —Å–ª–æ–≤–∞–º';
  }

  function handleTagsRewritter(foo) {
    if (foo - 3 <= 0) { return ''; }
    if (foo - 3 === 1) { return '-–º—É –¥—Ä—É–≥–æ–º—É'; }
    if (foo > 2) { return '-–º –¥—Ä—É–≥–∏–º'; }
    return '';
  }

  return (
    <section className='summary'>
      <div className="summary__wrapper">
        <p className="summary__subtitle">–°–æ—Ö—Ä–∞–Ω–µ–Ω–Ω—ã–µ —Å—Ç–∞—Ç—å–∏</p>
        <h3 className="summary__title">{currentUser.name}, —É –≤–∞—Å {count > 0 ? count : '–µ—â—ë –Ω–µ—Ç'} —Å–æ—Ö—Ä–∞–Ω–µ–Ω–Ω{handleTitleSaveNewsRewritter(count)}</h3>
        {mySavedArticles.length > 0 && (
        <p className="summary__keyword">–ü–æ –∫–ª—é—á–µ–≤{handleSubtitleRewritter(mySavedKeywords.length)}:
          <span className="summary__keyword_type_bold"> {mySavedKeywords.length < 2 ? firstKeyword : keywords }</span>
          {mySavedKeywords.length > 2 && (
            <span className="summary__keyword_type_bold"> {
              (mySavedKeywords.length - 3) > 0
                ? `–∏ ${mySavedKeywords.length - 3}`
                : ''}
              {handleTagsRewritter(mySavedKeywords.length)}
            </span>
          )
          }
        </p>)
        }
      </div>
    </section>

  );
}

export default Summary;
