import React, { useEffect } from 'react';
import './Login.css';
import PopupWithForm from '../PopupWithForm/PopupWithForm';
import Validator from '../../utils/Validator';

function Login({
  isOpen, onClose, onSwitchPopup, onLogin, textError, isFieldDisabled,
}) {
  const {
    values,
    handleChange,
    errors,
    isValid,
    resetForm,
  } = Validator();

  useEffect(() => {
    resetForm();
  }, [isOpen, resetForm]);

  function handleSubmit(e) {
    e.preventDefault();
    onLogin(values.email, values.password);
  }

  return (
    <PopupWithForm
      isOpen={isOpen}
      onClose={onClose}
      onSwitchPopup={onSwitchPopup}
      onSubmit={handleSubmit}
      name='login'
      title='–í—Ö–æ–¥'
      text='–∏–ª–∏ '
      buttonDescription='–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è'>
        <label className="popup__label">Email</label>
        <input
          value={values.email || ''}
          onChange={handleChange}
          className="popup__input popup__input_type_email"
          id="email-login"
          type="email"
          name="email"
          placeholder="–í–≤–µ–¥–∏—Ç–µ –ø–æ—á—Ç—É"
          minLength="5"
          maxLength="30"
          autoComplete="on"
          disabled={isFieldDisabled}
          required
        />
        <span id="email-error-login" className="popup__error" >{errors.email || ''}</span>

      <label className="popup__label">–ü–∞—Ä–æ–ª—å</label>
        <input
          value={values.password || ''}
          onChange={handleChange}
          className="popup__input popup__input_type_password"
          id="password-login"
          type="password"
          name="password"
          placeholder="–í–≤–µ–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å"
          minLength="5"
          maxLength="30"
          autoComplete="on"
          disabled={isFieldDisabled}
          required
        />
        <span id="password-error-login" className="popup__error" >{errors.password || ''}</span>
        <span id="main-error-login" className="popup__error popup__error_type_login">{textError}</span>
        <button className={`popup__submit ${isValid ? 'popup__submit_type_active' : ''}`} type="submit" disabled={!isValid}>–í–æ–π—Ç–∏</button>
    </PopupWithForm>
  );
}

export default Login;
