import React, { useState } from 'react';
import './searchForm.css';
import { handleKeywordFormatter } from '../../utils/helpers';

function SearchForm({ handleNewsSearch }) {
  const [searchRequest, setSearchRequest] = useState('');
  const [requestError, setRequestError] = useState(false);

  function handleSubmitForm(e) {
    e.preventDefault();
    if (searchRequest !== '') {
      handleNewsSearch(searchRequest);
      setSearchRequest('');
      return setRequestError(false);
    }
    return setRequestError(true);
  }

  return (
    <section className="search">
      <form className="search__form" onSubmit={handleSubmitForm} noValidate>
        <h1 className="search__title">–ß—Ç–æ —Ç–≤–æ—Ä–∏—Ç—Å—è –≤ –º–∏—Ä–µ?</h1>
        <p className="search__subtitle">–ù–∞—Ö–æ–¥–∏—Ç–µ —Å–∞–º—ã–µ —Å–≤–µ–∂–∏–µ —Å—Ç–∞—Ç—å–∏ –Ω–∞ –ª—é–±—É—é —Ç–µ–º—É –∏ —Å–æ—Ö—Ä–∞–Ω—è–π—Ç–µ –≤ —Å–≤–æ—ë–º –ª–∏—á–Ω–æ–º –∫–∞–±–∏–Ω–µ—Ç–µ.</p>
        <div className="search__wrapper">
          <input
            value={searchRequest}
            className="search__input"
            placeholder={`${requestError ? '–í–∞–º –Ω—É–∂–Ω–æ –≤–≤–µ—Å—Ç–∏ –∫–ª—é—á–µ–≤–æ–µ —Å–ª–æ–≤–æ ‚ÄºÔ∏èÔ∏è' : '–í–≤–µ–¥–∏—Ç–µ —Ç–µ–º—É –Ω–æ–≤–æ—Å—Ç–∏'}`}
            onChange={ (e) => setSearchRequest(handleKeywordFormatter(e.target.value)) }
            required/>
          <button className="search__button">–ò—Å–∫–∞—Ç—å</button>
        </div>
      </form>
    </section>
  );
}

export default SearchForm;
