import React, { useState, useEffect } from 'react';
import { Switch, Route, useHistory } from 'react-router-dom';
import './app.css';

// –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—ã
import Header from '../Header/Header';
import Main from '../Main/Main';
import SearchForm from '../SearchForm/SearchForm';
import Articles from '../Articles/Articles';
import Footer from '../Footer/Footer';

// –ø–æ–ø–∞–ø—ã
import Register from '../Register/Register';
import Login from '../Login/Login';
import PopupConfirm from '../PopupConfirm/PopupConfirm';

// –∫–æ–Ω—Ç–µ–∫—Å—Ç –∏ HOC
import CurrentUserContext from '../../context/CurrentUserContext';
import ProtectedRoute from '../ProtectedRoute/ProtectedRoute';

// —Å–µ—Ä–≤–∏—Å–Ω—ã–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—ã
import Preloader from '../Preloader/Preloader';
import NoResult from '../NoResult/NoResult';

// –∞–ø–∏ –∏ —É—Ç–∏–ª–∏—Ç—ã
import * as mainApi from '../../utils/MainApi';
import getArticlesFromServer from '../../utils/NewsApi';

function App() {
  const [isLoggedIn, setIsLoggedIn] = useState(false); // –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–æ–Ω–Ω—ã–π —Å—Ç–µ–π—Ç
  const [currentUser, setCurrentUser] = useState({}); // —Å—Ç–µ–π—Ç –¥–∞–Ω–Ω—ã—Ö –ø–æ —é–∑–µ—Ä—É
  const [formFieldDisabled, setFormFieldDisabled] = useState(false); // —Å—Ç–µ–π—Ç –±–ª–æ–∫–∏—Ä–æ–≤–∫–∏ –∏–Ω–ø—É—Ç–æ–≤ –ø—Ä–∏ –æ—Ç–ø—Ä–∞–≤–∫–µ. —Ö–æ—Ç—è –ø–æ —É–º—É –ª—É—á—à–µ –±—ã –±–ª–æ—á–∏—Ç—å —Ñ–æ—Ä–º—É

  // —Å—Ç–µ–π—Ç—ã –ø–æ–∫–∞–∑–∞ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤ –≤ –ø–æ–∏—Å–∫–µ —Å—Ç–∞—Ç–µ–π
  const [openPreloader, setOpenPreloader] = useState(false);
  const [openNoResults, setOpenNoResults] = useState(false);

  // —Å—Ç–µ–π—Ç—ã –æ—Ç–∫—Ä—ã—Ç–∏—è –ø–æ–ø–∞–ø–æ–≤
  const [openRegisterPopup, setOpenRegisterPopup] = useState(false);
  const [openLoginPopup, setOpenLoginPopup] = useState(false);
  const [openConfirmPopup, setOpenConfirmPopup] = useState(false);
  const [burgerOpen, setBurgerOpen] = useState(false);

  // –ø–æ–∫–∞–∑ –æ—à–∏–±–æ–∫ –≤ –ø–æ–ø–∞–ø–µ
  const [showErrorInPopup, setShowErrorInPopup] = useState(''); // –≤—ã–≤–æ–¥ –æ—à–∏–±–æ–∫ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ –≤ –ø–æ–ø–∞–ø–µ
  const [searchError, setSearchError] = useState(false); // –≤—ã–≤–æ–¥ –æ—à–∏–±–æ–∫ –ø–æ–∏—Å–∫–∞ –≤ noResult

  // —Å—Ç–µ–π—Ç—ã –¥–∞–Ω–Ω—ã—Ö —Å —Å–µ—Ä–≤–µ—Ä–∞ newsApi
  const [articles, setArticles] = useState([]);
  const [keyword, setKeyword] = useState('');
  const [mySavedArticles, setMySavedArticles] = useState([]);

  const history = useHistory();

  function getSavedNews() {
    mainApi.getAllArticles()
      .then((data) => {
        setMySavedArticles(data);
        setKeyword(data.keyword);
      })
      .catch((err) => {
        Promise.reject(new Error(`Error: ${err}`));
      });
  }

  function tokenCheck() {
    const jwt = localStorage.getItem('jwt');
    if (jwt) {
      setIsLoggedIn(true);
      getSavedNews();
      mainApi.getUserContent(jwt)
        .then((res) => {
          if (res) {
            setCurrentUser(JSON.parse(localStorage.getItem('user')));
            setIsLoggedIn(true);
          }
        })
        .catch((err) => {
          Promise.reject(new Error(`Error: ${err}`));
        });
    }
  }

  useEffect(() => {
    if (localStorage.getItem('articles') !== null || undefined) {
      setArticles(JSON.parse(localStorage.getItem('articles')));
    }
  }, []);

  useEffect(() => {
    tokenCheck();
  }, [isLoggedIn]);

  useEffect(() => {
    setKeyword(localStorage.getItem('keyword'));
  }, [keyword]);

  function handleClickBurger() {
    setBurgerOpen(!burgerOpen);
  }

  function closeAllPopups() {
    setOpenRegisterPopup(false);
    setOpenLoginPopup(false);
    setOpenConfirmPopup(false);
  }

  function handleOpenRegisterPopup() {
    setShowErrorInPopup(null);
    setOpenRegisterPopup(true);
  }

  function handleOpenLoginPopup() {
    setShowErrorInPopup(null);
    setOpenConfirmPopup(false);
    setOpenLoginPopup(true);
  }

  function handlePopupSwitcher() {
    if (openRegisterPopup) {
      closeAllPopups();
      handleOpenLoginPopup();
    }
    if (openLoginPopup) {
      closeAllPopups();
      handleOpenRegisterPopup();
    }
  }

  useEffect(() => {
    function handleOverlayClose(e) {
      if (e.target.classList.contains('popup_active')) {
        closeAllPopups();
      }
    }

    function handleEscClose(e) {
      if (e.key === 'Escape') {
        closeAllPopups();
      }
    }

    document.addEventListener('click', handleOverlayClose);
    document.addEventListener('keydown', handleEscClose);

    return () => {
      document.removeEventListener('click', handleOverlayClose);
      document.removeEventListener('keydown', handleEscClose);
    };
  }, []);

  function handleRegister(email, password, name) {
    setFormFieldDisabled(true);
    mainApi.createUser(email, password, name)
      .then((res) => {
        if (res) {
          closeAllPopups();
          setOpenConfirmPopup(true);
          history.push('./');
          setFormFieldDisabled(false);
        }
      })
      .catch((errCode) => {
        if (errCode === 409) {
          setFormFieldDisabled(false);
          return setShowErrorInPopup('Email —É–∂–µ —Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–ª—Å—è');
        }
        if (errCode === 429) {
          setFormFieldDisabled(false);
          return setShowErrorInPopup('–ü—Ä–µ–≤—ã—à–µ–Ω–æ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –∑–∞–ø—Ä–æ—Å–æ–≤');
        }
        setShowErrorInPopup('–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫, –ø–æ–ø—Ä–æ–±—É–π—Ç–µ –ø–æ–∑–∂–µ');
        return setFormFieldDisabled(false);
      });
  }

  function handleLogin(email, password) {
    setFormFieldDisabled(true);
    mainApi.authorizeUser(email, password)
      .then((res) => {
        if (res) {
          localStorage.clear();
          setArticles([]);
          localStorage.setItem('jwt', res.token);
          mainApi.getUserContent(res.token)
            .then((userData) => {
              localStorage.setItem('user', JSON.stringify(userData));
              setCurrentUser(userData);
              setIsLoggedIn(true);
              setOpenNoResults(false);
              setSearchError(null); // –º–æ–∂–Ω–æ —Å—Ç–∞–≤–∏—Ç—å false
              setKeyword('');
              localStorage.setItem('login', true);
              closeAllPopups();
              setFormFieldDisabled(false);
              history.push('./');
            })
            .catch((err) => {
              setFormFieldDisabled(false);
              setShowErrorInPopup(err);
            });
        }
      })
      // eslint-disable-next-line consistent-return
      .catch((err) => {
        setFormFieldDisabled(false);
        if (err === 401) {
          return setShowErrorInPopup('–ù–µ–ø—Ä–∞–≤–∏–ª—å–Ω—ã–π –ª–æ–≥–∏–Ω –∏–ª–∏ –ø–∞—Ä–æ–ª—å');
        }
        if (err === 400) {
          return setShowErrorInPopup('–¢–∞–∫–æ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –Ω–µ —Å—É—â–µ—Å—Ç–≤—É–µ—Ç!');
        }
        return setShowErrorInPopup('–°–µ—Ä–≤–∏—Å –Ω–∞ –ø—Ä–æ—Ñ–∏–ª–∞–∫—Ç–∏–∫–µ. –ü–æ–≤—Ç–æ—Ä–∏—Ç–µ –ø–æ–ø—ã—Ç–∫—É –ø–æ–∑–¥–Ω–µ–µ');
      });
  }

  function handleLogout() {
    setIsLoggedIn(false);
    setOpenNoResults(false);
    setArticles([]);
    localStorage.clear();
    history.push('/');
  }

  function handleSearchNews(tag) {
    setOpenNoResults(false);
    setOpenPreloader(true);
    getArticlesFromServer(tag)
      .then((data) => {
        localStorage.setItem('articles', JSON.stringify(data.articles));
        localStorage.setItem('keyword', tag);
        setArticles(data.articles);
        setKeyword(tag);
        if (data.articles.length === 0) {
          setSearchError(false);
          setOpenNoResults(true);
        }
        setOpenPreloader(false);
      })
      .catch((err) => {
        setOpenPreloader(false);
        setOpenNoResults(true);
        setSearchError(err.message);
      });
  }

  function handleSaveNews(article, tag) {
    if (isLoggedIn) {
      mainApi.saveArticle(article, tag)
        .then(() => getSavedNews())
        .catch((err) => Promise.reject(new Error(`Error: ${err}`)));
    }
    return '';
  }

  function handleDeleteSavedNews(article) {
    mainApi.deleteArticle(article)
      .then(() => {
        const myArticleArray = mySavedArticles.filter((i) => (i._id !== article._id));
        setMySavedArticles(myArticleArray);
      })
      .catch((err) => {
        Promise.reject(new Error(`Error ${err}`));
      });
  }

  function handleUpdateMySavedArticles(article, tag, mySavedNews) {
    const mySavedArticle = mySavedArticles.find((i) => {
      if (mySavedNews) {
        return i.title === mySavedNews.title && i.text === mySavedNews.text;
      }
      if (article) {
        return i.title === article.title && i.text === article.description;
      }
      return null;
    });
    if (mySavedArticle) {
      return handleDeleteSavedNews(mySavedArticle);
    }
    return handleSaveNews(article, tag);
  }

  return (
    <div className='page'>
      <CurrentUserContext.Provider value={currentUser}>
        <Switch>
          <Route exact path='/'>
            <Header
              isOpenBurgerMenu={burgerOpen}
              isOpen={handleClickBurger}
              isOpenLoginPopup={handleOpenLoginPopup}
              isLogout={handleLogout}
              isLoggedIn={isLoggedIn}
            />
            <SearchForm
              handleNewsSearch={handleSearchNews}
            />
            <Preloader
              isOpen={openPreloader}
            />
            <NoResult
              isOpen={openNoResults}
              searchError={searchError}
            />
            <Main
              articles={articles}
              mySavedArticles={mySavedArticles}
              keyword={keyword}
              isLoggedIn={isLoggedIn}
              handleOpenLoginPopup={handleOpenLoginPopup}
              updateMySavedArticles={handleUpdateMySavedArticles}
            />
          </Route>
          <Route path='/saved-news'>
            <Header
              isOpenBurgerMenu={burgerOpen}
              isOpen={handleClickBurger}
              isOpenLoginPopup={handleOpenLoginPopup}
              isLogout={handleLogout}
              isLoggedIn={isLoggedIn}
            />
            <ProtectedRoute path='/saved-news'
              component={Articles}
              isLoggedIn={isLoggedIn}
              handleOpenLoginPopup={handleOpenLoginPopup}
              updateMySavedArticles={handleUpdateMySavedArticles}
              mySavedArticles={mySavedArticles}
              keyword={keyword}
            />
          </Route>
        </Switch>
        <Footer/>
        <section className="popups">
          <Register
            isOpen={openRegisterPopup}
            onClose={closeAllPopups}
            onSwitchPopup={handlePopupSwitcher}
            onRegister={handleRegister}
            textError={showErrorInPopup}
            isFieldDisabled={formFieldDisabled}
          />
          <Login
            isOpen={openLoginPopup}
            onClose={closeAllPopups}
            onSwitchPopup={handlePopupSwitcher}
            onLogin={handleLogin}
            textError={showErrorInPopup}
            isFieldDisabled={formFieldDisabled}
          />
          <PopupConfirm
           isOpen={openConfirmPopup}
           onClose={closeAllPopups}
           onLogin={handleOpenLoginPopup}
          />
        </section>
      </CurrentUserContext.Provider>
    </div>
  );
}

export default App;
